.menu-wrapper {
	list-style: none;
	margin: 0;
	min-width: 180px;
	max-height: 70vh;
	padding: 4px 8px;
	background-color: var(--background-sec);
	box-shadow: rgb(0 0 0 / 12%) 0px 1px 6px;
	color: var(--text-color);
	border-radius: 6px;
	transition: all 0.5s ease-out;
	overflow: auto;
}
.menu-item {
	display: flex;
    justify-content: space-between;
    padding: 8px 0;
	gap: 6px;
	align-items: center;
	text-transform: capitalize;
	font-size: 0.75rem;
	font-weight: 300;
    font-family: sans-serif;
}
